<template>
  <div class="swaps" style="height: 100vh">
    
    <div class="container">
      <div class="toolbar">
        <div class="toolbar-title">

          <span>Swap</span>
            
        </div>


      </div>
      <div class="swap-items">
       <div class="box-wrap">
        <div class="coin-box" style="margin-bottom: 10px">
          <div class="top">
              <div>
              <div class="coin-select">
                <img v-if="value1 == '1'" src="@/assets/img/usdc.svg" >
                <img v-if="value1 == '2'" src="@/assets/img/usdt.svg" >
                <img v-if="value1 == '3'" src="@/assets/img/cfx.svg" >
                <img v-if="value1 == '4'" width="40px" height="40px"  src="@/assets/img/pcfx.svg" >
              
                  <vs-select 
                    placeholder=""
                    v-model="value1"
                    color="#e91e62"
                    state="success"
                    background="#e91e62"
                    >
                    <!--<vs-option label="USDC" value="1" >
                      USDC
                    </vs-option>
                    <vs-option label="USDT" value="2" >
                      USDT
                    </vs-option>-->
                    <vs-option label="CFX" value="3" >
                      CFX
                    </vs-option>
                    <vs-option label="PCFX" value="4" >
                      PCFX
                    </vs-option>
                  </vs-select>
            
                </div>
              </div>

              <input class="token-amount-input" inputmode="decimal" 
                      autocomplete="off" 
                      autocorrect="off" 
                      type="text" 
                      @input="topInput"
                      placeholder="0.0"
                      @keypress="isNumber($event)"
                      pattern="^[0-9]*[.,]?[0-9]*$" v-model="topBalance" />
              <!--<vs-input
                class="top-balance"
                placeholder="0.0"
                v-model="topBalance"
                />-->
          </div>
          <div class="bottom"><span @click="topAvailableClick">Available: {{ toFixed(topAvailable, 4) }}</span></div>
        </div>
        <div class="center-box" @click="handleSwitch()"><img width="18px" height="18px" style="color: #fff" src="@/assets/img/arrow-down.svg" /></div>
        
        <div class="coin-box" style="margin-top: 10px">
          <div class="top">
              <div>
              <div class="coin-select">
                <img v-if="value2 == '1'" src="@/assets/img/usdc.svg" >
                <img v-if="value2 == '2'" src="@/assets/img/usdt.svg" >
                <img v-if="value2 == '3'" src="@/assets/img/cfx.svg" >
                <img v-if="value2 == '4'" width="40px" height="40px" src="@/assets/img/pcfx.svg" >
              
                  <vs-select 
                    placeholder="USDT"
                    v-model="value2"
                    color="#e91e62"
                    state="success"
                    background="#e91e62"
                    >
                    <!--<vs-option label="USDC" value="1" >
                      USDC
                    </vs-option>
                    <vs-option label="USDT" value="2" >
                      USDT
                    </vs-option>-->
                    <vs-option label="CFX" value="3" >
                      CFX
                    </vs-option>
                    <vs-option label="PCFX" value="4" >
                      PCFX
                    </vs-option>
                  </vs-select>
            
                </div>
              </div>

              <input class="token-amount-input" inputmode="decimal" 
                      autocomplete="off" 
                      autocorrect="off" 
                      type="text" 
                      placeholder="0.0"
                      @keypress="isNumber($event)"
                      spellcheck="false"
                      pattern="^[0-9]*[.,]?[0-9]*$" v-model="bottomBalance" />
              <!--<vs-input
                class="top-balance"
                placeholder="0.0"
                v-model="bottomBalance"
                />-->
          </div>
          <div class="bottom"><span @click="bottomAvailableClick">Available: {{ toFixed(bottomAvailable, 4) }}</span></div>
        </div>
       </div>

       <div class="exchange-info">
          <div class="receive">
            <div class="left">Receive(min)</div>
            <div class="right">{{ toFixed(minReceive, 2) }}</div>
          </div>
          <div class="slippage">
            <div class="left">Slippage tolerance</div>
            <div class="right">0%</div>
          </div>
          <div class="price">
            <div class="left">Price</div>
            <div class="right">1{{ coinName(value1) }} = 1{{ value2 == 1 ? "USDC" : coinName(value2) }}</div>
          </div>
       </div>
        <vs-button
          gradient
          :loading="loading"
          class="swapButton"
          @click="swap"
          > {{ !account ? $t('unlock') : $t('swap') }} </vs-button>

      </div>
    </div>

  </div>
</template>

<script>
import { mapActions } from "vuex";
import { BigNumber } from "bignumber.js";

export default {
  name: "swap-view",
  components: {
    //Collapsible,
  },
  data() {
    return {
      active: 0,
      loading: false,
      value1: '3',
      value2: '4',
      minReceive: 0,
      topBalance: null,
      topAvailable: 345.2324343,
      bottomBalance: null,
      bottomAvailable: 324.444334333,
    };
  },
  computed: {
    pcfxContract: function () {
      return this.$store.state.web3.pcfxContract;
    },
    account: function() {
      return this.$store.state.web3.account
    },
    balance: function() {
      return this.$store.state.web3.balance
    },
    pcfxBalance: function() {
      return this.$store.state.web3.pcfxBalance
    }
  },
  watch: {
    balance: async function(newValue, oldValue) { //update balance
      this.loadBalance()
    },
    pcfxBalance: async function(newValue, oldValue) { //update balance
      this.loadBalance()
    },
    value1: async function(newValue, oldValue) {
      console.log(newValue)
      console.log(oldValue)
      this.value2 = oldValue
    },
    value2: async function(newValue, oldValue) {
      this.value1 = oldValue
    }
  },
  async mounted() {
    this.loadBalance()
  },
  methods: {
    ...mapActions(['cfxLogin']),

    async swap() {
      if (!this.account) {
        await this.cfxLogin()
      } else {
        // cfx -> pcfx
        if (this.value1 == '3') {  // cfx -> pcfx
          

          const that = this
          if (that.$store.state.web3.isPortalInstalled == false) {
            that.$vs.notification({
              // width:'100%',
              duration: 10000,
              icon: "<i class='bx bx-error' ></i>",
              progress: 'auto',
              color: 'danger',
              position: 'top-center',
              title: that.$t('error'),
              text: that.$t('noticeWallet'),
            })
            return;
          }
          if (that.$store.state.web3.account == null) {
            that.$vs.notification({
              // width:'100%',
              duration: 10000,
              icon: "<i class='bx bx-error' ></i>",
              progress: 'auto',
              color: 'danger',
              position: 'top-center',
              title: that.$t('error'),
              text: that.$t('noticeAddr'),
            })
            return;
          }

          if (that.topBalance == null || that.topBalance <=0){
            this.$vs.notification({
              // width:'100%',
              duration: 10000,
              icon: "<i class='bx bx-error' ></i>",
              progress: 'auto',
              color: 'danger',
              position: 'top-center',
              title: that.$t('error'),
              text: that.$t('correctNumPlaceholder'),
            })
            return;
          }

          this.loading = true
          let status = await that.pcfxContract.deposit(that.$store.state.web3.account, that.topBalance)
          console.log(status)
          if (status.succ == true) {
            this.$vs.notification({
              // width:'100%',
              duration: 6000,
              icon: "<i class='bx bx-select-multiple' ></i>",
              progress: 'auto',
              color: 'success',
              position: 'top-center',
              title: that.$t('info'),
              text: that.$t('noticeCfxTranslate'),
            })
          } else {
            this.$vs.notification({
              // width:'100%',
              duration: 10000,
              icon: "<i class='bx bx-error' ></i>",
              progress: 'auto',
              color: 'danger',
              position: 'top-center',
              title: that.$t('error'),
              text: status.message,
            })
          }
        } else if (this.value1 == '4') {  // pcfx -> cfx
          this.loading = true
          const that = this
          let status = await that.pcfxContract.withdraw(that.$store.state.web3.account, that.topBalance);

          if (status.succ == true) {
            this.$vs.notification({
              // width:'100%',
              duration: 6000,
              icon: "<i class='bx bx-select-multiple' ></i>",
              progress: 'auto',
              color: 'success',
              position: 'top-center',
              title: that.$t('info'),
              text: that.$t('noticePcfxTranslate'),
            })
          } else {
            this.$vs.notification({
              // width:'100%',
              duration: 10000,
              icon: "<i class='bx bx-error' ></i>",
              progress: 'auto',
              color: 'danger',
              position: 'top-center',
              title: that.$t('error'),
              text: status.message,
            })
          }
        }

        this.loading = false
        this.updateCfxAndPcfxBalance()
        this.loadBalance()
      }
    },
    async updateCfxAndPcfxBalance() {
      let that = this;
      let pcfxBalance = await that.pcfxContract.balanceOf(that.$store.state.web3.account);
      that.$store.commit('UPDATE_PCFX_BALANCE', pcfxBalance);
      let balance = await this.$store.state.web3.confluxReader.getBalance(that.$store.state.web3.account)
      that.$store.commit('UPDATE_BALANCE', BigNumber(balance).div(BigNumber(10 ** 18)))
    },
    loadBalance() {
      if (this.value1 == '3') {
        this.topAvailable = this.$store.state.web3.balance
      } else if(this.value1 == '3') {
        this.topAvailable = this.$store.state.web3.pcfxBalance
      }

      if (this.value2 == '3') {
        this.bottomAvailable = this.$store.state.web3.balance
      } else if (this.value2 == '4') {
        this.bottomAvailable = this.$store.state.web3.pcfxBalance
      }
    },
    topInput() {
      this.minReceive = this.topBalance
    },
    handleSwitch() {
      var oldVal1 = this.value1
      this.value1 = this.value2
      this.value2 = oldVal1

      //update the balance
      this.updateUi()
    },
    topAvailableClick() {
      this.topBalance = this.topAvailable
      this.topInput()
    },
    bottomAvailableClick() {
      this.bottomBalance = this.bottomAvailable
    },
    updateUi() {
      if (this.value1 == '3') {  //cfx
        // update available balance
        this.topAvailable = this.balance
      } else if (this.value1 == '4') { //pcfx
        this.topAvailable = this.pcfxBalance
      }

      if (this.value2 == '3') {  //cfx
        this.bottomAvailable = this.balance
      } else if (this.value2 == '4') { //pcfx
        this.bottomAvailable = this.pcfxBalance
      }

      this.minReceive = this.topBalance
    },
    coinName(value) {
      if (value == '1') {
        return 'USDC'
      } else if (value == '2') {
        return 'USDT'
      } else if (value == '3') {
        return 'CFX'
      } else if (value == '4') {
        return 'PCFX'
      }
    },
    toFixed(number, fractionDigits) {
      var times = Math.pow(10, fractionDigits)
      var roundNum = Math.round(number * times) / times
      return roundNum.toFixed(fractionDigits)
    },
    isNumber: function(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();;
      } else {
        return true;
      }
    }
  },
};
</script>

